.main-content {
  position: relative;

  &__box {
    // min-height: 70vh;
    width: 100%;
    position: relative;
    background-color: $color-white;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    margin-bottom: 3rem;

    &--inner-wrapper,
    &--header,
    &--footer {
      padding: 1.2rem;
    }
    &--inner-wrapper {
      .form__box{
        height: 8rem;
      }
    }
    &--header {
      border-radius: 4px 4px 0px 0px;
      background-color: $color-content-light;
    }
    &--body {
      //padding: 0 1.2rem;
      h2 {
        margin: 0;
      }
      .btn__link.danger {
        margin: 0;
      }
      span {
        margin-left: 0.5rem;
      }
      .form__span {
        left: 68%;
      }
      .text-area {
        background-color: #f9fafb;
        color: #111827;
        font-size: 14px;
        padding: 1.2rem;
        border-radius: 4px;
        -webkit-border-radius: 4px;
        border: 1px solid #d1d5db;
        width: 100%;
        &:focus {
          outline: none;
          padding: 1.1rem;
          border: 2px solid #6b7280;
          border-radius: 4px;
        }
      }
      .form__box {
        height: 9rem;
      }
    }
    &--footer {
      background-color: $color-white;
      border-radius: 0px 0px 4px 4px;
    }
    .hr-main {
      border: 0;
      height: 1px;
      background-color: #d1d5db;
      margin-bottom: 0;
    }

    // .search__icon {
    //   width: 20px;
    //   height: 30px;
    //   fill: #000000;
    // }
  }
  &__search-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 1.5rem;
    .search {
      width: 100%;
      &__input {
        background-color: $color-background-input;
        width: 100%;
        padding: 1rem 4rem;
      }
    }
    .select {
      background-color: $color-background-input;
      color: $color-text-dark;
      font-size: $font-size-default;
      padding: 1rem;
      border-radius: 4px;
      -webkit-border-radius: 4px;
      border: 1px solid $border-input-lines;
      width: 100%;
      box-sizing: border-box;
    }
  }
}

.select {
  background-color: $color-background-input;
  color: $color-text-dark;
  font-size: $font-size-default;
  padding: 1rem;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  border: 1px solid $border-input-lines;
  width: 100%;
  box-sizing: border-box;
}

.react-datepicker-wrapper{
  width: 100%;
  margin-top: 20px;
}
#main-content {
  // position: absolute;
  //left: 13rem;
  transition: left .2s cubic-bezier(.17,.67,.83,.67);
}
