.fiscal-bill {
  position: relative;
  font-family: "Inconsolata", monospace!important;
  font-size: 24px;
  background-color: $color-white;
  height: 100vh;
  padding: 10px;

  &__header {
    position: relative;
    &--logo {
      text-align: center;
    }
    &--info {
      margin-bottom: 2rem;
    }
    p {
      text-align: center;
    }
    p:nth-last-child {
      margin-bottom: 2rem;
    }
  }
  &__body {
    table {
      h2 {
        font-size: 30px;
        font-weight: 700;
        margin-bottom: 4rem;
      }
      border-top: 1px solid $color-black;
      width: 100%;
      tr:first-child {
        border-top: 1px solid $color-black;
      }
      td {
        border-bottom: none;
      }
      td {
        padding: 0;
        vertical-align: top;
      }
      .left {
        width: 75%;
      }
    }
  }
  &__footer {
      position: relative;
      &--qr-code {
          text-align: center;
          margin-top: 2rem;
          img {
              height: 12rem;
              width: 12rem;
          }
      }
      p {
          font-size: 18px!important;
      }
  }

  p {
    font-size: 24px;
    line-height: 25px;
  }
 
}
