.table-wrapper-grid-view {
  display: flex;
  padding: 0 1.5rem;
}
.table-grid-view {
  width: 100%;
  border-collapse: collapse;
  border: none;
  font-size: $font-size-grid-view;
  tbody {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    tr {
      position: relative;
      display: flex;
      flex-direction: column;
      width: 16rem;
      height: 16rem;
      background-color: $color-content-light;
      border: 1px solid #eee;
      border-radius: 16px;
      margin-bottom: 1rem;
      padding: 1.2rem;
      overflow: hidden;
      cursor: pointer;
      .stepper {
        position: absolute;
        bottom: -40px;
        left: 0;
        opacity: 0;
        transition: opacity 0.1.5s ease-in-out;
        transition: bottom 0.1.5s 0.1s ease-in-out;
      }
    }
    .active {
      position: relative;
      background: $color-content-lines;
      border: 2px solid $color-background-dark;
      width: 15.9rem;
      height: 15.9rem;
      .stepper {
        position: absolute;
        bottom: 10px;
        left: 0;
        justify-content: center;
        width: 100%;
        opacity: 1;
      }
    }
  }
  th {
    background-color: $color-content-light;
    color: white;
    font-weight: 500;
    span {
      display: flex;
      flex-direction: column;
      .desc {
        font-size: 10px;
      }
      .asc {
        font-size: 10px;
        transform: rotate(180deg);
      }
    }
    button {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      background-color: transparent;
      border: none;
      //color: white;
      outline: none;
      cursor: pointer;
    }
    input {
      border-radius: 5px;
      border: none;
      padding: 5px 10px;
      outline: none;
    }
  }
  td {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    p {
      width: 100%;
      font-size: $font-size-grid-view;
    }
    span {
      margin-right: 1rem;
    }
  }
  th {
    padding: 1.5rem;
  }
  // th:first-child {
  //   width: 35%;
  //   @include respond(tab-land) {
  //     width: 40%;
  //   }
  // }
  // th:last-child {
  //   width: 20%;
  //   text-align: right;

  //   @include respond(tab-land) {
  //     width: 15%;
  //   }
  // }
  td:nth-child(2){
    display: none;
  }
  td:nth-child(3){
    position: absolute;
    bottom: 4.5rem;
  }
  td,
  th {
    text-align: left;
    border-bottom: none;
  }

  .inner-td-wrapper {
    display: flex;
    align-items: center;
  }
  .td-title {
    margin-left: 2rem;
  }
}
